@import "utils";

.gallery {

  &__caption {
    margin-top: 24px;
    margin-bottom: 40px;

    &_link {
      cursor: pointer;
      color: $c-sky_blue;
      font-weight: bold;

      &:active {
        color: #3e3e3e;
      }
    }
  }

  &__button {
    width: 188px;
    height: 56px;
    margin: auto;
  }
}
